import './styles/style.scss';
import Home from './Components/Home Page/Home'
import ComposerPage from './Components/ComposerPage'
import React, {useState, useEffect} from 'react';
import  {Switch, Route} from 'react-router-dom'


function App() {
  const [search, setSearch] = useState('')
  const [wikiExtract, setWikiExtract] = useState([])


  /* const url = `https://en.wikipedia.org/w/api.php?action=opensearch&search=${search}&format=json&origin=*`; */

  const url = `https://en.wikipedia.org/w/api.php?action=query&format=json&prop=images%7Cextracts%7Crevisions&titles=Miles%20Davis&formatversion=latest&imlimit=3&exlimit=1&rvprop=content&rvslots=*&rvsection=0&origin=*`;

 /*  https://www.wikidata.org/w/api.php?action=query&format=json&prop=extracts%7Cimageinfo&titles=Miles_Davis&formatversion=latest&exlimit=20&explaintext=1&iiprop=url */

  async function makeSearch(){
      const response = await fetch(url)
      const jsonRes = await response.json()

      await setWikiExtract(Object.entries(Object.entries(jsonRes)[2][1])[0][1][0].extract)
  
      
      console.log();
  }

  useEffect(() => {
      makeSearch();
  }, []);


  return (
    <div className="App">
      {/* <div dangerouslySetInnerHTML={{__html: wikiExtract}}/> */}
      <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/composerpage" component={ComposerPage}/>
      </Switch>
    </div>
  );
}

export default App;
